<template>
  <section class="works__cases">
    <div class="box">
      <div class="section__title">
        <div class="h3">My Works</div>
        <p>Here are listed the technology that will be identified by me in the development process</p>
      </div>
    </div>
    <div class="works__cases_items" v-masonry transition-duration="0.3s" item-selector=".work">
      <div v-masonry-tile v-for="(w, index) in allWorks" v-if="index < maxCases" :key="w.id" class="work">
        <router-link :to="{name: 'Work', params: {id: w.id, title: w.title}}" class="work__item">
          <div class="work__box-image">
            <img class="work__image" :src="w.imageThumbs" alt="">
          </div>
          <div class="work__button work__button_thumb">
            <h5 class="h5 work__title work__title_thumb">{{ w.title }}</h5>
            <div class="work__line"></div>
            <div class="work__button_text"><span>view more</span><icon class="work__button_icon" name="angle-right"></icon></div>
          </div>
        </router-link>
      </div>
    </div>
    <div class="works__button">
      <router-link to="/works" class="button">view more</router-link>
    </div>
  </section>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'
  import 'vue-awesome/icons'

  export default {
    props: ['maxCases'],
    mounted () {
      this.getAllWorks()
    },
    computed: {
      ...mapGetters([
        'allWorks'
      ])
    },
    methods: {
      ...mapActions([
        'getAllWorks'
      ])
    }
  }

</script>

<style scoped lang="stylus">
@require '../assets/styles/variables.styl'

.works
  &__cases
  padding-top 15px
    &_items
      margin 0 7px
    .section
      &__title
        text-align center
        max-width 100%
  &__button
    text-align center

.work
  width calc(20% - 14px)
  margin 0 7px 14px 7px
  overflow: hidden
  cursor pointer
  border-radius 6px
  &__button
    position absolute
    bottom 0
    width 100%
    padding 4px 5px
    transition all .6s
    background-color #fff
    border-top 1px solid #d6d6d6
    &_text
      text-transform uppercase
      font-size 10px
      text-align: center
      color #303030
      font-weight: 600
    &_icon
      margin-left 6px
      margin-top -3px
      display inline-block
      vertical-align middle
  &__box-image
    overflow: hidden
    position relative
    transition: all .6s
    &:after
      position absolute
      top 0
      left 0
      width 100%
      height 100%
      transition all .6s
      border-radius 6px
      content ''
      background-color rgba(#ffffff, 0.7)
      z-index 1
      cursor pointer
  &__item
    display block
    font-size 0
  &__image
    width 100%
    transition: all .7s
  &__line
    width 80%
    height 1px
    background-color #d6d6d6
    margin: 0 auto 4px auto
  &__title
    color #212121
    font-weight 400
    margin-bottom 4px
    &_thumb
      text-align center
  &:hover
    .work__box-image:after
      opacity 0

@media screen and (max-width: 1200px)
  .work
    width 25%

@media screen and (max-width: 992px)
  .work
    width 33.3%

@media screen and (max-width: 768px)
  .work
    width 50%

@media screen and (max-width: 480px)
  .work
    width calc(100% - 14px)
    &__button
      bottom: 0
    &__box-image
      &:after
        display none
</style>



