<template>
  <section class="works background__dark">
    <navbar></navbar>
    <works-cases v-bind:max-cases="100"></works-cases>
  </section>
</template>

<script>
  import Navbar from '../components/Navbar.vue'
  import WorksCases from '../components/WorksCases.vue'
  import { mapGetters, mapActions } from 'vuex'

  export default {
    metaInfo () {
      return {
        title: 'Works cases'
      }
    },
    components: {
      Navbar,
      WorksCases
    },
    mounted () {
      this.getAllWorks()
    },
    computed: {
      ...mapGetters([
        'allWorks'
      ])
    },
    methods: {
      ...mapActions([
        'getAllWorks'
      ])
    }
  }
</script>

<style scoped lang="stylus">

  .works
    font-size: 0
    padding 60px 0 0 0

</style>



